import React, { Component } from 'react';

class Loading extends Component{
  Intervel = null
  state = {
    loadingDot : '.'
  }
  componentDidMount(){

    this.Intervel =  setInterval(() => this.setState({loadingDot : this.state.loadingDot + '.'}), 1000);
  }
  componentWillUnmount(){
    clearInterval(this.Intervel);
  }
  render(){
    return (
      <p>Loading {this.state.loadingDot}</p>
    )
  }
}

export default Loading;


